import axios from '@/libs/api.request'

/**
 *  获取设备类型列表
* @param pageSize 类型名
 * @param pageNumber 类型code
 * @param typeName 描述
 * @returns {*}
 */
export const getTypeList = ({ pageSize,pageNumber,typeName }) => {
  const data = {
    pageSize,pageNumber,typeName
  };
  return axios.request({
    url: 'equipmentType/listEquipmentType',
    params: data,
    method: 'get'
  })
};

export const getTypeDetail = ({id}) => {
  const data = {
    id
  };
  return axios.request({
    url: 'equipmentType/listEquipmentType'+id,
    params: data,
    method: 'get'
  })
};

/**
 *  新建设备
 * @param typeName 类型名
 * @param typeCode 类型code
 * @param description 描述
 * @returns {*}
 */
export const getNewList = ({ typeName, typeCode, description }) => {
  const data = {
    typeName,
    typeCode,
    description
  };
  return axios.request({
    url: `equipmentType/addEquipmentType`,
     data,
    method: 'post'
  })
};

/**
 * 编辑设备类型
 * @param id id
 * @param typeName 类型名
 * @param typeCode 类型code
 * @param description 描述
 * @returns {*}
 */
export const getEditList = ({id, typeName, typeCode, description}) => {
    const data = {
        id,
        typeName,
        typeCode,
        description
      };
    return axios.request({
      url: 'equipmentType/editEquipmentType',
      data,
      method: 'post'
    })
  };



    /**
 * 删除设备类型
 * @param id id
 * @param typeName 类型名
 * @param typeCode 类型code
 * @param description 描述
 * @returns {*}
 */
export const deleteInfo = ({id,typeName,typeCode,description}) => {
    const data = {
        id,typeName,typeCode,description
      };
    return axios.request({
      url: 'equipmentType/delEquipmentType',
       data,
      method: 'post'
    })
  };





