<!---设备类型-->
<template>
  <div class="device-type-manage">
    <div class="content">
      <!-- <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">设备类型</div>
          <span class="empty"></span>
        </div>
      </div> -->
      <div class="filter-search">
        <div class="filter-search-left">
          <div class="filter-input">
            <Input placeholder="请输入设备类型"
                   v-model="formFilter.typeName"
                   clearable
                   @keyup.enter.native="getType()" />
          </div>
          <div class="filter-btn half-border">
            <Button @click="getType()"
                    class="search-btn"
                    :loading="serLoading"
                    type="default">
              <span class="iconfont icon-chazhao"></span>
            </Button>
          </div>
        </div>
        <div class="filter-search-right">
          <span class="add-btn">
            <Button class="btn-create"
                    type="default"
                    @click="clickshow">
              <span class="iconfont icon-a-lianhe68"></span>
              新增
            </Button>
          </span>
        </div>
      </div>
      <div class="user-table">
        <Table class="m-table no-scroll no-border"
               :loading="loading"
               stripe
               :row-class-name="(row,index)=>index%2===1?'even':''"
               :columns="tableColumns"
               :data="tableData"></Table>
      </div>
      <div class="pagination">
        <Page @on-change="onPaginationChange"
              @on-page-size-change="onPaginationSizeChange"
              :current="pageInfo.pageIndex"
              :page-size="pageInfo.pageSize"
              :total="pageInfo.totalElements"
              show-elevator
              show-sizer
              show-total />
        <!--<div class="page-total">共{{pageInfo.totalElements}}条</div>-->
      </div>
    </div>
    <custom-drawer ref="drawer"
                   :title="title"
                   :formValidate="formValidate"
                   :formList="formList"
                   v-model="formValue"
                   @confirm="confirm"></custom-drawer>
  </div>
</template>

<script>
import {
  deleteInfo,
  getEditList,
  getNewList,
  getTypeList
} from '@/api/deviceType'
import customDrawer from '@/components/common-drawer/common-drawer'

export default {
  name: 'device-type-manage',
  components: {
    customDrawer
  },
  data () {
    return {
      formFilter: {
        typeName: ''
      },
      title: '',
      modalNew: false,
      formValue: {},
      formList: [
        {
          type: 'input',
          label: '设备类型',
          placeholder: '请输入设备类型',
          value: 'typeName'
        },
        {
          type: 'input',
          label: '设备编码',
          placeholder: '请输入设备编码',
          value: 'typeCode'
        },
        {
          type: 'textarea',
          label: '备注',
          placeholder: '请输入备注内容',
          value: 'description'
        }
      ],
      formValidate: {
        typeName: [
          {
            required: true,
            message: '设备类型不能为空',
            trigger: 'blur'
          }
        ],
        typeCode: [
          {
            required: true,
            message: '设备编码不能为空',
            trigger: 'blur'
          }
        ]
      },
      loading1: false,
      loading2: false,
      loading: false,
      serLoading: false,
      tableColumns: [
        {
          title: '设备类型',
          key: 'typeName',
          align: 'left'
        },
        {
          title: '类型编码',
          key: 'typeCode',
          align: 'left'
        },
        {
          title: '描述',
          key: 'description',
          align: 'left'
        },
        {
          title: '操作',
          key: 'action',
          width: 200,
          align: 'left',
          render: (h, params) => {
            return h('div', [
              this.$hasAccess('type.edit') &&
                h(
                  'span',
                  {
                    class: 'primary',
                    style: {
                      marginLeft: 0,
                      color: '#0084FF'
                    },
                    on: {
                      click: () => {
                        this.editInfo(params)
                      }
                    }
                  },
                  '编辑'
                ),
              this.$hasAccess('type.del') &&
                h(
                  'span',
                  {
                    class: 'secondary',
                    style: {
                      color: '#F12121'
                    },
                    on: {
                      click: () => {
                        this.deleteInfo(params.row)
                      }
                    }
                  },
                  '删除'
                )
            ])
          }
        }
      ],
      tableData: [],
      pageInfo: {
        pageIndex: 1,
        pageSize: 10,
        totalElements: 0
      },
      type: ''
    }
  },
  methods: {
    showColumn () {
      if (!this.$hasAccess('type.edit') && !this.$hasAccess('type.del')) {
        this.tableColumns.splice(this.tableColumns.length - 1, 1)
      }
    },
    clickshow () {
      this.$refs.drawer.open()
      this.title = '新建设备类型'
      this.type = 'add'
      this.formValue = {
        typeName: '',
        typeCode: '',
        description: ''
      }
    },
    handleSearch () {},
    handleReset (name) {
      // 或者手动将各个属性置空
      this.$refs[name].resetFields()
    },
    getType () {
      this.serLoading = true
      this.loading = true
      this.pageInfo.pageIndex = 1
      this.loadTableData()
    },
    addCancel () {
      // 或者手动将各个属性置空
      // this.$router.go(-1);
      this.modalNew = false
    },
    addSet () {
      this.loading1 = true
      if (this.formValue.id) {
        getEditList(this.formValue).then(
          (res) => {
            this.loading1 = false
            this.modalNew = false
            if (res.code === 200) {
              this.$Message.info(this.$t('common.message.edit_success'))
              // this.$router.push({ name: 'device-type-manage' })
              this.$refs.drawer.close()
              this.loadTableData()
            } else {
              if (res.code == 1000) {
                this.$Message.warning(res.msg)
              } else {
                this.$Message.warning(this.$t('common.message.edit_fail'))
              }
            }
          }
        )
      } else {
        let data = {
          description: this.formValue.description,
          typeCode: this.formValue.typeCode,
          typeName: this.formValue.typeName
        }

        getNewList(data).then(
          (res) => {
            this.loading1 = false
            if (res.code === 200) {
              this.$Message.success(this.$t('common.message.add_success'))
              this.modalNew = false
              this.$refs.drawer.close()
              this.loadTableData()
            } else {
              if (res.code == 1000) {
                this.$Message.warning(res.msg)
              } else {
                this.$Message.warning(this.$t('common.message.add_fail'))
              }
            }
          }
        )
      }
    },

    loadTableData (clear) {
      if (clear) {
        this.pageInfo.pageIndex = 1
      }
      this.loading = true
      getTypeList({
        typeName: this.formFilter.typeName,
        pageNumber: this.pageInfo.pageIndex,
        pageSize: this.pageInfo.pageSize
      }).then((res) => {
        this.loading = false
        this.serLoading = false
        this.tableData = (res.data.result || []).map((d) => ({
          ...d
        }))
        this.pageInfo.totalElements = res.data.totalElements || 0
      })
    },
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    },
    editInfo (params) {
      this.formValue = Object.assign({}, params.row)
      this.title = '编辑设备类型'
      this.type = 'edit'
      this.$refs.drawer.open()
    },
    deleteInfo (uInfo) {
      this.$Modal.confirm({
        title: this.$t('common.text.prompt'),
        width: this.$flexible(450),
        content:
          this.$t('common.text.confirm_title', {
            name: this.$t('common.text.device_type')
          }) + `[${uInfo.typeName}]的设备类型吗?`,
        onOk: () => {
          deleteInfo({
            id: uInfo.id
          }).then(
            (res) => {
              if (res.code === 200) {
                this.$Message.success(this.$t('common.message.delete_success'))
                this.loadTableData()
              } else if (res.code === 1000) {
                this.$Message.warning(res.msg)
              } else {
                this.$Message.warning(this.$t('common.message.delete_fail'))
              }
            }
          )
        }
      })
    },
    confirm (flag) {
      if (flag) {
        this.addSet()
      } else {
        this.isBtnLoading = false
      }
    }
  },
  mounted () {
    this.loadTableData(true)
    this.showColumn()
  }
}
</script>

<style lang="less" scoped>
@import './../../../style/varient';

@size-padding: 10px;
.device-type-manage {
  width: 100%;
  height: 100%;

  .content {
    width: 100%;
    height: 100%;
    overflow: auto;
    padding: 24px;
    margin-bottom: 10px;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
  }
}
</style>



