import axios from '@/libs/api.request'

/**
 *  获取设备类型列表
 * @param pageNumber 页码
 * @param pageSize 页数
 * @param name 型号名
 * @param typeId 类型id 
 * @returns {*}
 */
export const getModelList = ({ pageNumber, pageSize, name, typeId }) => {
    const data = {
        pageNumber,
        name,
        typeId,
        pageSize
    };
    return axios.request({
        url: 'equipmentModel/listEquipmentModel',
        params: data,
        method: 'get'
    })
};

/**
 *  获取保养项目列表
 * @param pageNumber 页码
 * @param pageSize 页数
 * @param modelId 类型id 
 * @returns {*}
 */
export const itemList = ({ modelId, pageNumber, pageSize }) => {
    const data = {
        modelId,
        pageNumber,
        pageSize
    };
    return axios.request({
        url: 'maintenance/itemList',
        params: data,
        method: 'get'
    })
};


/**
 *  保养项目添加(编辑)
 *  @param modelId 类型id 
 *  @param content 保养内容 
 *  @param maintenanceTime 保养时间 
 *  @param level 类别
 *  @param description 描述
 * @returns {*}
 */
export const Edititem = ({ modelId, content, maintenanceTime, level, description }) => {
    const data = {
        modelId,
        content,
        maintenanceTime,
        level,
        description
    };
    return axios.request({
        url: 'maintenance/saveItem',
        data,
        method: 'post'
    })
};

/**
 *  保养项目删除
 *  @param id 类型id 
 * @returns {*}
 */
export const Delitem = ({ id }) => {
    const data = {
        id
    };
    return axios.request({
        url: 'maintenance/delItem',
        params: data,
        method: 'get'
    })
};


/**
 *  获取设备类型列表
 * @param pageNumber 页码
 * @param pageSize 页数
 * @returns {*}
 */
export const getModelType = ({}) => {
    let data = {}
    return axios.request({
        url: 'equipmentModel/listEquipmentModelAll',
        params: data,
        method: 'get'
    })
};

/**
 *  根据id获取型号详细信息
 * @param id 型号id
 * @returns {*}
 */
export const getModleDetail = ({ id }) => {
    return axios.request({
        url: `equipmentModel/` + id,
        params: {},
        method: 'get'
    })
};


/**
 *  新建设备
 * @param modelName 型号名
 * @param typeId 类型id
 * @param manufacturerId 制造商id
 * @param description 描述
 * @param maintenanceTemplateId 报告模板id
 * @param maintenanceApprovale 是否审批 1-是 0-否
 * @param maintenanceReminable 是否提醒 1-是 0-否
 * @param reminderTime 时间
 * @param unit 单位 1-小时 2-天
 * @param repairTemplateId 报告模板id
 * @param repairApprovale 是否审批 1-是 0-否
 * @returns {*}
 */
export const getNewList = (params) => {
    return axios.request({
        url: `equipmentModel/addEquipmentModel`,
        data: params,
        method: 'post'
    })
};


/**
 *  编辑设备类型
 * @param modelName 型号名
 * @param typeId 类型id
 * @param manufacturerId 制造商id
 * @param description 描述
 * @param maintenanceTemplateId 报告模板id
 * @param maintenanceApprovale 是否审批 1-是 0-否
 * @param maintenanceReminable 是否提醒 1-是 0-否
 * @param reminderTime 时间
 * @param unit 单位 1-小时 2-天
 * @param repairTemplateId 报告模板id
 * @param repairApprovale 是否审批 1-是 0-否
 * @returns {*}
 */
export const getEditModel = (params) => {
    return axios.request({
        url: `equipmentModel/editEquipmentModel`,
        data: params,
        method: 'post'
    })
};

/**
 * 查询设备类型
 * @param typeName id
 * @returns {*}
 */
export const serchTypeList = (params) => {
    return axios.request({
        url: 'equipmentType/listEquipmentType',
        params,
        method: 'get'
    })
};

/**
 * 删除设备型号
 * @param id id
 * @returns {*}
 */
export const deleteInfoCode = ({ id }) => {
    const data = {
        id
    };
    return axios.request({
        url: 'equipmentModel/delEquipmentModel',
        data,
        method: 'post'
    })
};


