<!---设备型号-->
<template>
  <div class="device-manufacture-manage">
    <div class="content">
      <!-- <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">设备制造商</div>
          <span class="empty"></span>
        </div>
      </div> -->
      <div class="filter-search">
        <div class="filter-search-left">
          <div class="filter-input mr-12">
            <Input placeholder="请输入制造商名称"
                   v-model="formFilter.name"
                   clearable
                   @keyup.enter.native="handleSearch" />
          </div>
          <div class="filter-btn">
            <Button @click="handleSearch"
                    :loading="serLoading"
                    class="search-btn"
                    type="default">
              <span class="iconfont icon-chazhao"></span>
            </Button>
          </div>
        </div>
        <div class="filter-search-right">
          <span class="add-btn">
            <Button type="default"
                    @click="showDrawer('add')">
              <span class="iconfont icon-a-lianhe68"></span>
              新增
            </Button>
          </span>
        </div>
      </div>
      <div class="user-table">
        <Table class="m-table no-scroll no-border"
               :row-class-name="(row,index)=>index%2===1?'even':''"
               stripe
               :loading="loading"
               :columns="tableColumns"
               :data="tableData"></Table>
      </div>
      <div class="pagination">
        <Page @on-change="onPaginationChange"
              @on-page-size-change="onPaginationSizeChange"
              :current="pageInfo.pageIndex"
              :page-size="pageInfo.pageSize"
              :total="pageInfo.totalElements"
              show-elevator
              show-sizer
              show-total />
        <!--<div class="page-total">共{{pageInfo.totalElements}}条</div>-->
      </div>
    </div>
    <custom-drawer ref="drawer"
                   :title="title"
                   :formValidate="formValidate"
                   :formList="formList"
                   v-model="formValue"
                   @confirm="confirm"></custom-drawer>
  </div>
</template>

<script>
// import TypeSelect from "@c/device-type-select";
import {
  deleteManuInfo,
  getEidtManu,
  getManuList,
  getNewManu
} from '@/api/deviceManufac'
import customDrawer from '@/components/common-drawer/common-drawer'

export default {
  name: 'device-manufacture-manage',
  components: {
    customDrawer
  },
  data () {
    return {
      type: '',
      formList: [
        {
          label: '制造商名',
          type: 'input',
          value: 'manufactruer'
        },
        {
          label: '联系人',
          type: 'input',
          value: 'contact'
        },
        {
          label: '联系电话',
          type: 'input',
          value: 'phone'
        },
        {
          label: '邮箱',
          type: 'input',
          value: 'email'
        },
        {
          label: '传真',
          type: 'input',
          value: 'fax'
        },
        {
          label: '地址',
          type: 'input',
          value: 'address'
        },
        {
          label: '备注',
          type: 'textarea',
          value: 'description'
        }
      ],
      formValue: {},
      formValidate: {
        manufactruer: [
          {
            required: true,
            message: '设备制造商不能为空',
            trigger: 'blur'
          }
        ],
        contact: [
          {
            required: true,
            message: '联系人不能为空',
            trigger: 'blur'
          }
        ],
        // email: [{ type: 'email', message: '邮箱不能为空', trigger: 'blur' }],
        phone: [
          {
            required: true,
            message: '联系电话不能为空',
            trigger: 'blur'
          },
          {
            validator: (rule, value, callback) => {
              if (value) {
                let isPhone =
                  /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$|400[0-9]{7}|^800[0-9]{7}/
                if (!isPhone.test(value)) {
                  callback('电话格式错误')
                } else {
                  callback()
                }
              } else {
                callback()
              }
            },
            trigger: 'blur'
          }
        ]
      },
      loading1: false,
      formFilter: {
        name: ''
      },
      loading: false,
      searchKey: [],
      title: '',
      tableColumns: [
        {
          title: '设备制造商',
          key: 'manufactruer',
          align: 'left'
        },
        {
          title: '联系人',
          key: 'contact',
          align: 'left'
        },
        {
          title: '联系电话',
          key: 'phone',
          align: 'left'
        },
        {
          title: '传真',
          key: 'fax',
          align: 'left'
        },
        {
          title: '邮箱',
          key: 'email',
          align: 'left'
        },
        {
          title: '地址',
          key: 'address',
          align: 'left'
        },
        {
          title: '操作',
          key: 'action',
          width: 200,
          align: 'left',
          render: (h, params) => {
            return h('div', [
              this.$hasAccess('maker.edit') &&
                h(
                  'span',
                  {
                    class: 'primary',
                    style: {
                      marginLeft: 0,
                      color: '#0084FF'
                    },
                    on: {
                      click: () => {
                        this.showDrawer('edit', params)
                      }
                    }
                  },
                  '编辑'
                ),
              this.$hasAccess('maker.del') &&
                h(
                  'span',
                  {
                    class: 'secondary',
                    style: {
                      color: '#F12121'
                    },
                    on: {
                      click: () => {
                        // console.log(params)
                        this.deleteInfo(params.row)
                      }
                    }
                  },
                  '删除'
                )
            ])
          }
        }
      ],
      serLoading: false,
      tableData: [],
      pageInfo: {
        pageIndex: 1,
        pageSize: 10,
        totalElements: 0
      }
    }
  },
  methods: {
    addItem () {
      this.title = '新增设备制造商'
      this.formValue = {}
      this.showDrawer = true
    },
    addSet () {
      this.loading1 = true
      if (this.formValue.id) {
        getEidtManu({ ...this.formValue }).then(
          (res) => {
            this.loadTableData()
            this.loading1 = false
            if (res.code === 200) {
              this.$Message.success(this.$t('common.message.edit_success'))
              // this.$router.push({ name: 'device-manufacture-manage' })
              this.$refs.drawer.close()
            } else {
              this.$Message.warning(
                this.$t('common.message.edit_fail') + ':' + res.msg
              )
            }
          }
        )
      } else {
        getNewManu({ ...this.formValue }).then(
          (res) => {
            this.loadTableData()
            this.loading1 = false
            if (res.code === 200) {
              this.$Message.success(this.$t('common.message.add_success'))
              // this.$router.push({ name: 'device-manufacture-manage' })
              this.$refs.drawer.close()
            } else {
              this.$Message.warning(
                this.$t('common.message.add_fail') + ':' + res.msg
              )
            }
          }
        )
      }
    },
    showColumn () {
      if (!this.$hasAccess('maker.edit') && !this.$hasAccess('maker.del')) {
        this.tableColumns.splice(this.tableColumns.length - 1, 1)
      }
    },
    handleReset (name) {
      // 或者手动将各个属性置空
      this.$refs[name].resetFields()
    },
    handleSearch () {
      this.serLoading = true
      //   this.insideTableData = this.value.filter(item => item[this.searchKey].indexOf(this.searchValue) > -1)
      this.pageInfo.pageIndex = 1
      this.loadTableData()
    },
    loadTableData (clear) {
      this.loading = true
      if (clear) {
        this.pageInfo.pageIndex = 1
      }

      this.searchKey = this.tableData.map((item) => {
        return item.deviceType
      })
      // console.log(111, this.searchKey);

      // this.pageInfo.totalElements = this.tableData.length;
      let data = {
        pageNumber: this.pageInfo.pageIndex,
        pageSize: this.pageInfo.pageSize,
        name: this.formFilter.name
      }

      getManuList(data).then((res) => {
        this.loading = false
        this.serLoading = false
        this.tableData = (res.data.result || []).map((d) => ({
          ...d
        }))
        this.pageInfo.totalElements = res.data.totalElements || 0
      })
    },
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    },
    editInfo (params) {
      this.formValue = { ...params.row }
      this.showDrawer = true
    },
    confirm (flag) {
      if (flag) {
        this.addSet()
      } else {
        this.loading1 = false
      }
    },
    deleteInfo (uInfo) {
      // console.log(uInfo)
      this.$Modal.confirm({
        title: this.$t('common.text.prompt'),
        width: this.$flexible(450),
        content:
          '确认删除名称为' +
          `[${uInfo.manufactruer}]的设备制造商吗?`,
        onOk: () => {
          deleteManuInfo({
            id: uInfo.id
          }).then(
            (res) => {
              if (res.code === 200) {
                this.$Message.success(this.$t('common.message.delete_success'))
                this.loadTableData()
              } else if (res.code === 1000) {
                this.$Message.warning(res.msg)
              } else {
                this.$Message.warning(this.$t('common.message.delete_fail'))
              }
            }
          )
        }
      })
    },
    showDrawer (type, params) {
      this.type = type
      this.$refs.drawer.open()
      if (type === 'add') {
        this.title = '新建设备制造商'
        this.formValue = {
          manufactruer: '',
          contact: '',
          phone: '',
          email: '',
          fax: '',
          address: '',
          description: ''
        }
      } else {
        this.title = '编辑设备制造商'
        let val = JSON.parse(JSON.stringify(params.row))
        this.formValue = val
      }
    }
  },
  mounted () {
    this.loadTableData(true)
    this.showColumn()
  }
}
</script>

<style lang="less" scoped>
@import './../../../style/varient';

@size-padding: 10px;
.device-manufacture-manage {
  width: 100%;
  height: 100%;

  .content {
    width: 100%;
    height: 100%;
    overflow: auto;
    padding: 24px;
    margin-bottom: 10px;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
  }
}
</style>



