<!---设备型号-->
<template>
  <div class="device-model-manage">
    <div class="content">
      <!-- <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">设备型号</div>
        </div>
      </div> -->
      <div class="filter-search">
        <div class="filter-search-left">
          <div class="filter-input mr-12">
            <Input placeholder="请输入设备型号"
                   v-model="formFilter.name"
                   clearable
                   @keyup.enter.native="handleSearch" />
          </div>
          <div class="filter-select mr-12">
            <StatusSelect v-model="formFilter.typeId"
                        clearable
                        :type= 1
                        placeholder="请选择设备类型"
                        ></StatusSelect>
          </div>
          <div class="filter-btn">
            <Button @click="handleSearch"
                    :loading="serLoading"
                    class="search-btn"
                    type="default">
              <span class="iconfont icon-chazhao"></span>
            </Button>
          </div>
        </div>
        <div class="filter-search-right">
          <span class="add-btn">
            <Button type="default"
                    @click="showDrawer('add')">
              <span class="iconfont icon-a-lianhe68"></span>
              新增
            </Button>
          </span>
        </div>
      </div>
      <div class="user-table">
        <Table class="m-table no-scroll no-border"
               :row-class-name="(row,index)=>index%2===1?'even':''"
               stripe
               :loading="loading"
               :columns="tableColumns"
               :data="tableData"></Table>
      </div>
      <div class="pagination">
        <Page @on-change="onPaginationChange"
              @on-page-size-change="onPaginationSizeChange"
              :current="pageInfo.pageIndex"
              :page-size="pageInfo.pageSize"
              :total="pageInfo.totalElements"
              show-elevator
              show-sizer
              show-total />
      </div>
    </div>
    <custom-drawer ref="drawer"
                   :title="title"
                   :formValidate="formValidate"
                   :formList="formList"
                   v-model="formValue"
                   @confirm="confirm"></custom-drawer>
  </div>
</template>

<script>
import StatusSelect from '@/components/device-select'
import { deleteInfoCode, getModelList, serchTypeList, getModleDetail, getNewList, getEditModel } from '@/api/deviceModel'
import {getManufacturer} from '@/api/device'
import customDrawer from '@/components/common-drawer/common-drawer'

export default {
  name: 'device-model-manage',
  components: { StatusSelect, customDrawer },
  data () {
    return {
      formFilter: {
        name: '',
        typeId: ''
      },
      serLoading: false,
      loading: false,
      searchKey: [],
      tableColumns: [
        {
          title: '设备型号',
          key: 'modelName',
          align: 'left'
        },
        {
          title: '设备类型',
          key: 'typeName',
          align: 'left'
        },
        {
          title: '设备制造商',
          key: 'manufacturerName',
          align: 'left'
        },
        {
          title: '描述',
          key: 'description',
          align: 'left'
        },
        {
          title: '操作',
          key: 'action',
          width: 200,
          align: 'left',
          render: (h, params) => {
            return h('div', [
              this.$hasAccess('model.query') &&
                h(
                  'span',
                  {
                    class: 'primary',
                    style: {
                      marginLeft: 0,
                      color: '#0084FF'
                    },
                    on: {
                      click: () => {
                        this.showDrawer('detail', params)
                      }
                    }
                  },
                  '编辑'
                ),
              this.$hasAccess('model.del') &&
                h(
                  'span',
                  {
                    class: 'secondary',
                    style: {
                      color: '#F12121'
                    },
                    on: {
                      click: () => {
                        this.deleteInfo(params.row)
                      }
                    }
                  },
                  '删除'
                )
            ])
          }
        }
      ],
      tableData: [],
      pageInfo: {
        pageIndex: 1,
        pageSize: 10,
        totalElements: 0
      },
      type: '',
      title: '',
      formList: [
        {
          label: '设备型号',
          type: 'input',
          value: 'modelName'
        },
        {
          label: '设备类型',
          type: 'select',
          value: 'typeName',
          data: []
        },
        {
          label: '设备制造商',
          type: 'select',
          value: 'manufacturerName',
          data: []
        },
        {
          label: '备注',
          type: 'textarea',
          value: 'description'
        }
      ],
      formValue: {},
      formValidate: {
        modelName: [
          {
            required: true,
            message: '设备型号不能为空',
            trigger: 'blur'
          }
        ],
        typeName: [
          {
            required: true,
            message: '请选择设备类型',
            trigger: 'change',
            type: 'number'
          }
        ],
        manufacturerName: [
          {
            required: true,
            message: '请选择设备制造商',
            trigger: 'change',
            type: 'number'
          }
        ],
        errorOrder: [
          {
            required: true,
            message: '请选择工单审批',
            trigger: 'change'
          }
        ],
        template: [
          {
            required: true,
            message: '请选择报告模板',
            trigger: 'change',
            type: 'number'
          }
        ],
        maintainOrder: [
          {
            required: true,
            message: '请选择工单审批',
            trigger: 'change'
          }
        ]
      },
      id: 0
    }
  },
  methods: {
    handleReset (name) {
      // 或者手动将各个属性置空
      this.$refs[name].resetFields()
    },

    handleSearch () {
      this.serLoading = true
      this.pageInfo.pageIndex = 1
      this.loadTableData()
    },
    loadTableData (clear) {
      this.loading = true
      if (clear) {
        this.pageInfo.pageIndex = 1
      }
      this.pageInfo.totalElements = this.tableData.length
      let data = {
        pageNumber: this.pageInfo.pageIndex,
        pageSize: this.pageInfo.pageSize,
        name: this.formFilter.name,
        typeId: this.formFilter.typeId
      }
      getModelList(data).then((res) => {
        this.tableData = (res.data.result || []).map((d) => ({
          ...d
        }))
        this.pageInfo.totalElements = res.data.totalElements || 0
        this.loading = false
        this.serLoading = false
      })
    },
    loadType () {
      serchTypeList().then(res => {
        if (res.code === 200) {
          this.formList[1].data = res.data.result.map(i => {
            return {
              name: i.typeName,
              value: i.id
            }
          })
        }
      })
      getManufacturer().then(res => {
        if (res.code === 200) {
          this.formList[2].data = res.data.map(i => {
            return {
              name: i.manufactruer,
              value: i.id
            }
          })
        }
      })
    },
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    },
    deleteInfo (uInfo) {
      this.$Modal.confirm({
        title: this.$t('common.text.prompt'),
        width: this.$flexible(450),
        content:
          this.$t('common.text.confirm_title', {
            name: this.$t('common.text.device_model')
          }) + `[${uInfo.modelName}]的设备型号吗?`,
        onOk: () => {
          deleteInfoCode({
            id: uInfo.id
          }).then(
            (res) => {
              if (res.code === 200) {
                this.$Message.success(this.$t('common.message.delete_success'))
                this.loadTableData()
              } else {
                this.$Message.warning(res.msg)
              }
            }

          )
        }
      })
    },
    showDrawer (type, params) {
      this.type = type
      this.$refs.drawer.open()
      if (type === 'add') {
        this.title = '新建设备型号'
      } else {
        let { row } = params
        this.title = '编辑设备型号'
        this.id = row.id
        getModleDetail({id: row.id}).then(res => {
          if (res.code === 200) {
            let {data} = res
            this.formValue = {
              modelName: row.modelName,
              typeName: data.typeId,
              manufacturerName: data.manufacturerId,
              description: row.description
            }
          }
        })
      }
    },
    add () {},
    confirm (flag) {
      let vals = {
        ...this.formValue,
        typeId: this.formValue.typeName,
        manufacturerId: this.formValue.manufacturerName
      }
      if (flag) {
        if (this.type === 'add') {
          getNewList(vals).then(res => {
            if (res.code === 200) {
              this.$Message.success(res.msg)
              this.$refs.drawer.close()
              this.loadTableData()
            } else {
              this.$Message.warning(res.msg)
            }
          })
        } else {
          getEditModel({...vals, id: this.id}).then(res => {
            if (res.code === 200) {
              this.$Message.success(res.msg)
              this.$refs.drawer.close()
              this.loadTableData()
            } else {
              this.$Message.warning(res.msg)
            }
          })
        }
      }
    }
  },
  mounted () {
    this.loadTableData(true)
    this.loadType()
  }
}
</script>

<style lang="less" scoped>
@import './../../../style/varient';

@size-padding: 10px;
.device-model-manage {
  width: 100%;
  height: 100%;

  .content {
    width: 100%;
    height: 100%;
    overflow: auto;
    padding: 24px;
    margin-bottom: 10px;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
  }
}
</style>



