<template>
  <Select class="device-type-select" :value="value"  :placeholder="placeholder" @input="onSelectionChange" clearable>
    <!--<Option value="" key="-1">请选择</Option>-->
    <Option v-for="item in options" :value="item.value" :key="item.value">{{ item.label }}</Option>
  </Select>
</template>

<script>

  import {Select} from 'iview'
  import {getDeviceTypeList} from '@/api/device'

  export default {
    name: "device-type-select",
    i18n_prefix:'component.device-type-select',
    data() {
      return {
        options: [],
        optionMap:{}
      }
    },
    props: {
      value: String,
      flag:Boolean,
      placeholder:String
    },
    model: {
      prop: 'value',
      event: 'selection'
    },
    methods: {
      loadTreeData() {
        let optionMap = {};
        getDeviceTypeList({}).then((res) => {
          this.options = (res.data.result || []).map((d) => {
            optionMap[d.id] = d;
            return {
              value: d.id+'',
              label: `${d.typeName}`
            }
          });
          if(this.flag){
            let arr = [
              {value:'',label:$t_p('lable')}
            ];
            this.options = [...arr,...this.options];
          }
          this.optionMap = optionMap;
        });


      },
      onSelectionChange(v) {
        this.$emit('selection', v);
        this.$emit('on-selection-change', this.optionMap[v]);
      }
    },
    mounted() {
      this.loadTreeData()
    },
    components: {
      Select
    }
  }
</script>

<style scoped>

  .device-type-select {
    color: #000;
  }
</style>



