<template>
  <div class="list">
    <div class="content">
      <!-- <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">测点配置</div>
          <span class="empty"></span>
        </div>
      </div> -->
      <div class="filter-search">
        <div class="filter-search-left">
          <div class="filter-select mr-12">
            <Input
              placeholder="输入测点名称"
              v-model="search.itemName"
              clearable
            ></Input>
          </div>
          <div class="filter-select mr-12">
            <Input
              placeholder="输入测点描述"
              v-model="search.alias"
              clearable
            ></Input>
          </div>
          <div class="filter-select mr-12">
            <Select
              placeholder="选择测点类型"
              v-model="search.pointType"
              clearable
            >
              <Option
                v-for="item in typeList"
                :value="item.value"
                :key="item.value"
                >{{ item.name }}</Option
              >
            </Select>
          </div>
          <div class="filter-select mr-12">
            <Select placeholder="选择设备" v-model="search.deviceId" clearable>
              <Option
                v-for="item in deviceList"
                :value="item.value"
                :key="item.value"
                >{{ item.name }}</Option
              >
            </Select>
          </div>
          <div class="filter-btn">
            <Button
              type="default"
              @click="loadTableData()"
              :loading="pageInfo.loading"
            >
              <span class="iconfont icon-chazhao"></span>
            </Button>
          </div>
        </div>
        <div class="filter-search-right">
          <span class="add-btn">
            <Button type="default" @click="showDrawer('add')">
              <span class="iconfont icon-a-lianhe68"></span>
              新增
            </Button>
          </span>
        </div>
      </div>
      <div class="user-table">
        <Table
          class="m-table no-scroll no-border"
          stripe
          :loading="pageInfo.loading"
          :columns="tableColumns"
          :row-class-name="rowClassName"
          @on-row-click="onRowClick"
          :data="tableData"
        ></Table>
      </div>
      <div class="pagination">
        <Page
          @on-change="onPaginationChange"
          @on-page-size-change="onPaginationSizeChange"
          :current="pageInfo.pageIndex"
          :page-size="pageInfo.pageSize"
          :total="pageInfo.totalElements"
          show-elevator
          show-sizer
          show-total
        />
      </div>
      <custom-drawer
        ref="drawer"
        :title="title"
        :formValidate="formValidate"
        :formList="formList"
        v-model="formValue"
        @selectTree="selectTree"
        @confirm="confirm"
      ></custom-drawer>
    </div>
  </div>
</template>

<script>
import StatusSelect from '@/components/device-select'
import LaboratoryList from '@/components/laboratory-list'
import customDrawer from '@/components/common-drawer/common-drawer'
import {
  getPointList,
  addPointList,
  editPointList,
  getPointByDeviceId,
  getVirtualTag
} from '@/api/device'
export default {
  components: {
    StatusSelect,
    LaboratoryList,
    customDrawer
  },
  data () {
    return {
      deviceList: [],
      search: {
        pointType: '',
        alias: '',
        itemName: ''
      },
      pageInfo: {
        loading: false,
        pageIndex: 1,
        pageSize: 10,
        totalElements: 30
      },
      typeList: [
        { name: '电压', value: 'VOLTAGE' },
        { name: '功率', value: 'POWER' },
        { name: '流量', value: 'FLOW' },
        { name: '温度', value: 'TEMPERATURE' },
        { name: '压力', value: 'PRESSURE' },
        { name: '电流', value: 'CURRENT' }
      ],
      tableData: [],
      maxlength: 50,
      tableColumns: [
        {
          title: '序号',
          key: 'itemId',
          align: 'left',
          width: 100
        },
        {
          title: '机组名称',
          key: 'deviceName',
          align: 'left',
          ellipsis: true,
          tooltip: true,
          width: 200
        },
        {
          title: '测点名称',
          key: 'itemName',
          align: 'left',
          width: 120
        },
        {
          title: '中文名称',
          key: 'chineseName',
          align: 'left',
          ellipsis: true,
          tooltip: true
        },
        {
          title: '英文名称',
          key: 'engName',
          align: 'left'
        },
        {
          title: '子系统',
          key: 'subChildName',
          align: 'left'
        },
        {
          title: '关联虚拟测点',
          key: 'virtualTag',
          align: 'left'
          // render: (h, params) => {
          //   return h('span', params.row.virtualTag === 'YES' ? '是' : '否')
          // }
        },
        {
          title: '测点描述',
          key: 'alias',
          align: 'left',
          width: 180,
          ellipsis: true,
          tooltip: true
        },
        {
          title: '公式',
          key: 'formula',
          align: 'left'
        },
        {
          title: '测点类型',
          key: 'pointType',
          align: 'left',
          render: (h, params) => {
            for (let item of this.typeList) {
              if (item.value === params.row.pointType) {
                return h('span', item.name)
              }
            }
          }
        },
        {
          title: '操作',
          key: 'action',
          width: 100,
          align: 'left',
          render: (h, params) => {
            return h('div', [
              h(
                'span',
                {
                  class: 'primary',
                  style: {
                    marginLeft: 0,
                    color: '#0084FF'
                  },
                  on: {
                    click: () => {
                      this.showDrawer('edit', params.row)
                    }
                  }
                },
                '编辑'
              )
            ])
          }
        }
      ],
      currentIndex: 0,
      title: '',
      formList: [
        {
          label: '测点名称',
          type: 'input',
          value: 'itemName',
          placeholder: '请输入测点名称'
        },
        {
          label: '测点类型',
          type: 'select',
          value: 'pointType',
          data: []
        },
        {
          label: '公式',
          type: 'textarea',
          value: 'formula',
          placeholder: '请输入公式'
        },
        {
          label: '关联虚拟测点',
          type: 'select',
          value: 'virtualTag',
          data: []
        },
        {
          label: '测点描述',
          type: 'input',
          value: 'alias',
          placeholder: '请输入测点描述'
        },
        {
          label: '英文名称',
          type: 'input',
          value: 'engName',
          placeholder: '请输入英文名称'
        },
        {
          label: '中文名称',
          type: 'input',
          value: 'chineseName',
          placeholder: '请输入中文名称'
        },
        {
          label: '设备名称',
          type: 'select',
          value: 'deviceId',
          data: []
        },
        {
          label: '子系统名称',
          type: 'input',
          value: 'subChildName',
          placeholder: '请输入子系统名称'
        }
      ],
      formValue: {},
      formValidate: {
        itemName: [{ required: true, message: '测点名称不能为空' }],
        // pointType: [{required: true, message: '测点类型不能为空'}],
        // deviceId: [{required: true, message: '设备名称不能为空'}],
        alias: [{ required: true, message: '测点描述不能为空' }]
      },
      type: ''
    }
  },
  methods: {
    selectTree (e) {
      this.formValue.addr = e
    },
    getType () {
      this.formList[1].data = this.typeList
      getPointByDeviceId().then(res => {
        if (res.code === 200) {
          this.deviceList = res.data.map(i => {
            return {
              name: i.deviceName,
              value: i.deviceId
            }
          })
          this.formList[7].data = res.data.map(i => {
            return {
              name: i.deviceName,
              value: i.deviceId
            }
          })
        }
      })
      getVirtualTag().then(res => {
        if (res.code === 200) {
          this.formList[3].data = res.data.map(i => {
            return {
              name: i,
              value: i
            }
          })
        }
      })
    },
    loadTableData (clear) {
      let { pageSize, pageIndex: pageNumber } = this.pageInfo
      let { alias, pointType, itemName, deviceId } = this.search
      this.pageInfo.loading = true
      if (clear) {
        this.pageInfo.pageIndex = 1
      }
      getPointList({
        pageSize,
        pageNumber,
        alias,
        pointType,
        itemName,
        deviceId
      }).then(res => {
        if (res.code === 200) {
          this.tableData = res.data.result
          this.pageInfo.totalElements = res.data.totalElements
          this.pageInfo.loading = false
        } else {
          this.tableData = []
          this.pageInfo.totalElements = 0
          this.pageInfo.loading = false
        }
      })
    },
    showDrawer (type, params) {
      if (type !== 'delete') {
        if (type === 'add') {
          this.title = '新增自定义测点'
          this.formValue = {}
          this.formValue.virtualTag = 'YES'
        } else {
          let val = JSON.parse(JSON.stringify(params))
          console.log(val)
          this.title = '编辑测点'
          this.formValue = val
          this.formValue.deviceId = Number(val.deviceId)
          this.formValue.virtualTag = val.virtualTag || 'NO'
        }
        this.type = type
        this.$refs.drawer.open()
      } else {
      }
    },
    onRowClick () {},
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    },
    rowClassName (row, index) {
      if (row.id == this.currentIndex) {
        return 'demo-table-info-row'
      }
    },
    confirm () {
      this.formValue.state = this.formValue.deviceStatus
      this.formValue.deviceId = this.formValue.deviceId.toString()
      if (!this.formValue.deviceName) {
        for (let item of this.deviceList) {
          if (item.value === this.formValue.deviceId) {
            this.formValue.deviceName = item.name
          }
        }
      }
      if (this.type === 'edit') {
        editPointList({ ...this.formValue }).then(res => {
          let { code, msg } = res
          if (code === 200) {
            this.$Message.success(this.$t('common.message.edit_success'))
            this.loadTableData()
            this.$refs.drawer.close()
          } else {
            this.$Message.warning(msg)
          }
        })
      } else {
        addPointList({ ...this.formValue }).then(res => {
          let { code, msg } = res
          if (code === 200) {
            this.$Message.success(this.$t('common.message.add_success'))
            this.loadTableData()
            this.$refs.drawer.close()
          } else {
            this.$Message.warning(msg)
          }
        })
      }
    }
  },
  mounted () {
    this.loadTableData()
    this.getType()
  }
}
</script>

<style lang="less" scoped>
@import "../../style/varient";
@size-padding: 10px;
/deep/.ivu-page-options-elevator input {
  padding-left: 0.1rem !important;
  text-align: center;
}
.list {
  width: 100%;
  height: 100%;
  .content {
    width: 100%;
    height: 100%;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 24px;
  }
}
</style>



