<template>
  <Select class="device-model-select" :value="value"  :placeholder="placeholder" @input="onSelectionChange">
<!--    <Option value="" key="-1">全部</Option>-->
    <Option v-for="item in options" :value="item.value" :key="item.value">{{ item.label }}</Option>
  </Select>
</template>

<script>

  import {Select} from 'iview'
  import {getDeviceModelList} from '@/api/device'

  export default {
    name: "device-model-select",
    data() {
      return {
        options: [],
        optionMap: {}
      }
    },
    props: {
      value: String,
      placeholder: String,
      type: String
    },
    model: {
      prop: 'value',
      event: 'selection'
    },
    watch: {
      type() {
        this.loadTreeData();
      }
    },
    methods: {
      loadTreeData() {
        if(!this.type){
          return
        }
        getDeviceModelList({
          typeId: this.type
        }).then((res) => {
          let optionMap = {};
          this.options = (res.data.result || []).map((d) => {
            optionMap[d.id] = d;
            return {
              value: d.id + '',
              key:d.id+'',
              label: `${d.modelName}`
            }
          });
          // let arr = [
          //   {value:'',label:'全部'}
          // ]
          // this.options=[...arr,...this.options];
          this.optionMap = optionMap;
        })
      },
      onSelectionChange(v) {
        this.$emit('selection', v);
        this.$emit('on-selection-change', this.optionMap[v]);
      }
    },
    mounted() {
      this.loadTreeData()
    },
    components: {
      Select
    }
  }
</script>

<style scoped>

  .device-model-select {
    color: #000;
  }
</style>



