<template>
  <div class="list">
    <div class="content">
      <!-- <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">配置文件</div>
          <span class="empty"></span>
        </div>
      </div> -->
      <div class="filter-search">
        <div class="filter-search-left">
          <div class="filter-select mr-12">
             <StatusSelect placeholder="选择设备类型"
                          :filterable ='true'
                          :type = 1
                          v-model="search.deviceType"></StatusSelect>
          </div>
          <div class="filter-select mr-12">
            <StatusSelect placeholder="选择设备名称"
                          :filterable ='true'
                          :type = 2
                          v-model="search.deviceName"></StatusSelect>
          </div>
          <div class="filter-select mr-12">
            <StatusSelect placeholder="选择设备编号"
                          :filterable ='true'
                          :type = 3
                          v-model="search.deviceCode"></StatusSelect>
          </div>
          <div class="filter-btn">
            <Button type="default"
                    @click="loadTableData"
                    :loading="pageInfo.loading">
              <span class="iconfont icon-chazhao"></span>
            </Button>
          </div>
        </div>
        <div class="filter-search-right">
          <span class="add-btn">
            <Button type="default"
                    @click="showDrawer('add')">
              <span class="iconfont icon-a-lianhe68"></span>
              新增
            </Button>
          </span>
        </div>
      </div>
      <div class="user-table">
        <Table class="m-table no-scroll no-border"
               stripe
               :loading="pageInfo.loading"
               :columns="tableColumns"
               :row-class-name="rowClassName"
               @on-row-click="onRowClick"
               :data="tableData"></Table>
      </div>
      <div class="pagination">
        <Page style=""
              @on-change="onPaginationChange"
              @on-page-size-change="onPaginationSizeChange"
              :current="pageInfo.pageIndex"
              :page-size="pageInfo.pageSize"
              :total="pageInfo.totalElements"
              show-elevator
              show-sizer
              show-total />
      </div>
      <custom-drawer ref="drawer"
                     :title="title"
                     :formValidate="formValidate"
                     :formList="formList"
                     v-model="formValue"
                     @changeSelect ="changeSelect"
                     @confirm="confirm"></custom-drawer>
    </div>
  </div>
</template>

<script>
import StatusSelect from '@/components/device-select'
import LaboratoryList from '@/components/laboratory-list'
import customDrawer from '@/components/common-drawer/common-drawer'
import { getConfiguration, getDeviceSearchList, addConfiguration, updateConfiguration } from '@/api/device'
export default {
  components: {
    StatusSelect,
    LaboratoryList,
    customDrawer
  },
  data () {
    return {
      search: {
        deviceType: '',
        deviceName: '',
        deviceCode: ''
      },
      pageInfo: {
        loading: false,
        pageIndex: 1,
        pageSize: 10,
        totalElements: 30
      },
      tableData: [],
      maxlength: 50,
      tableColumns: [
        {
          title: '设备名称',
          key: 'deviceName',
          align: 'left'
        },
        {
          title: '设备类型',
          key: 'deviceTypeName',
          align: 'left'
        },
        {
          title: '设备编号',
          key: 'deviceCode',
          align: 'left'
        },
        {
          title: '配置文件类型',
          key: 'fileType',
          align: 'left'
        },
        {
          title: '配置文件名称',
          key: 'fileName',
          align: 'left'
        },
        {
          title: '配置文件版本',
          key: 'fileVersion',
          align: 'left'
        },
        {
          title: '更新时间',
          key: 'createTime',
          align: 'left'
        },
        {
          title: '操作',
          key: 'action',
          width: 220,
          align: 'left',
          render: (h, params) => {
            return h('div', [
              h(
                'span',
                {
                  class: 'primary',
                  style: {
                    marginLeft: 0,
                    color: '#0084FF'
                  },
                  on: {
                    click: () => {

                    }
                  }
                },
                '文件上传并下发'
              ),
              h(
                'span',
                {
                  class: 'primary',
                  style: {
                    color: '#0084FF'
                  },
                  on: {
                    click: () => {
                      this.showDrawer('edit', params.row)
                    }
                  }
                },
                '编辑'
              )
            ])
          }
        }
      ],
      currentIndex: 0,
      title: '',
      formList: [
        {
          label: '设备名称',
          type: 'select',
          value: 'deviceName',
          data: [],
          disabled: this.type === 'edit'
        },
        {
          label: '设备类型',
          type: 'select',
          value: 'deviceType',
          data: [],
          disabled: true
        },
        {
          label: '设备编号',
          type: 'select',
          value: 'deviceCode',
          data: [],
          disabled: true
        },
        {
          label: '配置文件类型',
          type: 'input',
          value: 'fileType'
        },
        {
          label: '配置文件名称',
          type: 'input',
          value: 'fileName'
        },
        {
          label: '配置文件版本',
          type: 'input',
          value: 'fileVersion'
        }
      ],
      formValidate: {
        // model: [{ required: true, message: '功率控制模式不能为空' }]
      },
      formValue: {},
      type: '',
      info: []
    }
  },
  mounted () {
    this.loadTableData()
    this.getType()
  },
  methods: {
    changeSelect (e) {
      this.formValue.deviceName = e
      this.formValue.deviceType = e
      this.formValue.deviceCode = e
    },
    getType () {
      getDeviceSearchList().then(res => {
        let { code, data } = res
        if (code === 200) {
          this.info = data || []
          this.formList[0].data = data.map(i => {
            return {
              name: i.deviceName,
              value: i.id
            }
          })
          this.formList[1].data = data.map(i => {
            return {
              name: i.deviceType,
              value: i.id
            }
          })
          this.formList[2].data = data.map(i => {
            return {
              name: i.deviceCode,
              value: i.id
            }
          })
        }
      })
    },
    loadTableData () {
      let {pageIndex: pageNumber, pageSize} = this.pageInfo
      this.pageInfo.loading = true
      getConfiguration({...this.search, pageNumber, pageSize}).then(res => {
        let { code, data } = res
        this.pageInfo.loading = false
        if (code === 200) {
          this.tableData = data.result
          this.pageInfo.totalElements = data.totalElements
        }
      })
    },
    showDrawer (type, params) {
      this.type = type
      if (type === 'add') {
        this.title = '新建配置文件'
        this.formValue = {}
        this.formList[0].disabled = false
      } else {
        let val = JSON.parse(JSON.stringify(params))
        this.title = '编辑配置文件'
        this.formValue = val
        this.formValue.deviceName = val.deviceId
        this.formValue.deviceType = val.deviceId
        this.formValue.deviceCode = val.deviceId
        this.formList[0].disabled = true
      }
      this.$refs.drawer.open()
    },
    onRowClick () {},
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    },
    rowClassName (row, index) {
      if (row.id == this.currentIndex) {
        return 'demo-table-info-row'
      } else {

      }
    },
    confirm () {
      let { deviceName } = this.formValue
      let {info} = this
      for (let i in info) {
        let item = info[i]
        if (item.id === deviceName) {
          this.formValue.deviceName = item.deviceName
          this.formValue.deviceType = item.deviceType
          this.formValue.deviceCode = item.deviceCode
          this.formValue.deviceId = item.id
        }
      }
      if (this.type === 'add') {
        addConfiguration({...this.formValue}).then(res => {
          this.loadingNew = false
          this.modalNew = false
          if (res.code === 200) {
            this.$Message.success(this.$t('common.message.add_success'))
            this.loadTableData()
            this.$refs.drawer.close()
          } else {
            if (res.code === 1000) {
              this.$Message.warning(res.msg)
            } else {
              this.$Message.warning(this.$t('common.message.add_fail'))
            }
          }
        })
      } else {
        updateConfiguration({...this.formValue}).then(res => {
          this.loadingNew = false
          this.modalNew = false
          if (res.code === 200) {
            this.$Message.success(this.$t('common.message.edit_success'))
            this.loadTableData()
            this.$refs.drawer.close()
          } else {
            if (res.code === 1000) {
              this.$Message.warning(res.msg)
            } else {
              this.$Message.warning(this.$t('common.message.edit_fail'))
            }
          }
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import './../../../style/varient';
@size-padding: 10px;

.list {
  width: 100%;
  height: 100%;
  .content {
    width: 100%;
    height: 100%;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 24px;
  }
}
</style>



