import axios from '@/libs/api.request'

/**
 *  获取故障代码列表
 * @param pageSize 类型id
 * @param pageNumber 型号id
 * @param name  故障代码或名字
 * @returns {*}
 */
export const getErrList = ({ pageSize, pageNumber, name}) => {
  const data = {
    pageSize,
    pageNumber,
    name
  }
  return axios.request({
    url: 'malfunctionInfo/listMalfunctionInfo',
    params: data,
    method: 'get'
  })
}


export const errDetail = ({id}) => {
  return axios.request({
    url: `malfunctionInfo/${id}`,
    method: 'get'
  })
}


/**
 *  新建故障代码
 * @param typeId 类型id
 * @param modelId 型号id
 * @param malCode 故障代码
 * @param description 描述
 * @param malName 故障名
 * @returns {*}
 */
export const getNewCode = (params) => {
  return axios.request({
    url: `malfunctionInfo/addMalfunctionInfo`,
    data: params,
    method: 'post'
  })
}

/**
 * 编辑故障代码
 * @param id 故障代码id
 * @param typeId 类型id
 * @param modelId 型号id
 * @param malCode 故障代码
 * @param description 描述
 * @param malName 故障名
 * @returns {*}
 */
export const getEditCode = (params) => {
  return axios.request({
    url: `malfunctionInfo/editMalfunctionInfo`,
    data: params,
    method: 'post'
  })
}



    /**
 * 删除故障代码
 * @param id id
 * @returns {*}
 */
export const deleteCodeInfo = ({id}) => {
  const data = {
    id
  }
    return axios.request({
    url: 'malfunctionInfo/delMalfunctionInfo',
    data,
    method: 'post'
  })
}



