<template>
  <div class="platform">
    <tab :data="tabList" @change="changeTab" :current="tabIndex"></tab>
    <div class="content">
      <transition :enter-active-class="routerTransitionClass.enter"
                      :leave-active-class="routerTransitionClass.leave">
        <component :is="is"></component>
      </transition>
    </div>
  </div>
</template>

<script>
import tab from '@/components/common-tab'
import deviceTypeManage from './components/device-type-manage.vue'
import deviceModelManage from './components/device-model-manage.vue'
import deviceManufactureManage from './components/device-manufacture-manage.vue'
import pointManage from '../point-manage/point-manage.vue'
import softwareManage from './components/control-list.vue'
import faultCodeManage from './components/fault-code-manage.vue'
import configList from './components/config-list.vue'
import RouterTransition from '@/mixins/router-transition'

export default {
  name: 'platform',
  components: {
    tab,
    deviceTypeManage,
    deviceModelManage,
    deviceManufactureManage,
    softwareManage,
    pointManage,
    configList,
    faultCodeManage
  },
  mixins: [RouterTransition],
  data () {
    return {
      tabList: [
        {
          id: 1,
          name: '设备类型',
          com: deviceTypeManage
        },
        {
          id: 2,
          name: '设备型号',
          com: deviceModelManage
        },
        {
          id: 3,
          name: '设备制造商',
          com: deviceManufactureManage
        },
        // {
        //   id: 4,
        //   name: '配置文件',
        //   com: configList
        // },
        {
          id: 4,
          name: '软件管理',
          com: softwareManage
        },
        {
          id: 5,
          name: '测点配置',
          com: pointManage
        },
        {
          id: 6,
          name: '故障代码',
          com: faultCodeManage
        }
      ],
      tabIndex: 0,
      is: null
    }
  },
  created () {
    this.is = this.tabList[this.tabIndex].com
  },
  methods: {
    changeTab (index) {
      console.log(index)
      this.tabIndex = index
      this.is = this.tabList[index].com
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../style/varient';
@size-padding: 10px;
.platform{
  position: relative;
  width: 100%;
  height: 100%;
  .content{
    width: 100%;
    height: calc(~"100% - " 40px);
    // background-color: #fff;
  }
}
</style>



