<!---故障代码-->
<template>
  <div class="fault-code-manage">
    <div class="content">
      <!-- <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">故障类型</div>
        </div>
      </div> -->
      <div class="filter-search">
        <div class="filter-search-left">
          <div class="filter-input mr-12">
            <Input placeholder="输入故障代码/故障名称"
                   :maxlength="50"
                   v-model="formFilter.name"
                   clearable
                   @keyup.enter.native="handleSearch" />
          </div>
          <div class="filter-btn">
            <Button @click="handleSearch"
                    :loading="serLoading"
                    class="search-btn"
                    type="default">
              <span class="iconfont icon-chazhao"></span>
            </Button>
          </div>
        </div>
        <div class="filter-search-right">
          <span class="add-btn">
            <Button type="default"
                    @click="showDrawer('add')">
              <span class="iconfont icon-a-lianhe68"></span>
              新增
            </Button>
          </span>
        </div>
      </div>
      <div class="user-table">
        <Table class="m-table no-scroll no-border"
               :loading="serLoading"
               :row-class-name="(row,index)=>index%2===1?'even':''"
               stripe
               :columns="tableColumns"
               :data="tableData"></Table>
      </div>
      <div class="pagination">
        <Page :loading="loading"
              @on-change="onPaginationChange"
              @on-page-size-change="onPaginationSizeChange"
              :current="pageInfo.pageIndex"
              :page-size="pageInfo.pageSize"
              :total="pageInfo.totalElements"
              show-elevator
              show-sizer
              show-total />
        <!--<div class="page-total">共{{pageInfo.totalElements}}条</div>-->
      </div>
      <custom-drawer ref="drawer"
                     :title="title"
                     :formValidate="formValidate"
                     :formList="formList"
                     v-model="formValue"
                     @confirm="confirm"></custom-drawer>
    </div>
  </div>
</template>

<script>
import TypeSelect from '@c/device-type-select'
import ModelSelect from '@c/device-model-select'
import DeviceTypeSelect from '@/components/device-type-select'
import DeviceModelSelect from '@/components/device-model-select'
import customDrawer from '@/components/common-drawer/common-drawer'

import {
  deleteCodeInfo,
  getEditCode,
  getErrList,
  getNewCode
} from '@/api/faultCode'

export default {
  name: 'fault-code-manage',
  i18n_prefix: 'page.fault-code-manage',
  components: {
    TypeSelect,
    ModelSelect,
    DeviceTypeSelect,
    DeviceModelSelect,
    deleteCodeInfo,
    customDrawer
  },
  data () {
    return {
      formFilter: {
        name: ''
      },
      title: '',
      serLoading: false,
      formValue: {},
      loading: false,
      formList: [
        {
          label: '故障代码',
          type: 'input',
          value: 'malCode'
        },
        {
          label: '故障名称',
          type: 'input',
          value: 'malName'
        },
        {
          label: '对比值',
          value: 'compare',
          type: 'input',
          inputType: 'number'
        },
        {
          label: '等级',
          value: 'level',
          type: 'input',
          inputType: 'number'
        },
        {
          label: '故障代码类型',
          type: 'input',
          value: 'malType'
          // data: [
          //   {name: '非虚拟', value: 'NON-VIRTUAL'},
          //   {name: '虚拟', value: 'VIRTUAL'}
          // ]
        },
        {
          label: '备注',
          type: 'textarea',
          value: 'description'
        }
      ],
      formValidate: {
        malCode: [
          {
            required: true,
            message: '故障代码不能为空',
            trigger: 'blur'
          }
        ],
        malName: [
          {
            required: true,
            message: '故障名称不能为空',
            trigger: 'blur'
          }
        ]
      },
      loadingNew: true,
      loadingEdit: true,
      modalNew: false,
      modalEdit: false,
      deviceType: [],
      deviceModel: [],
      tableColumns: [
        {
          title: '故障代码',
          key: 'malCode',
          align: 'left'
        },
        {
          title: '故障名称',
          key: 'malName',
          align: 'left'
        },
        {
          title: '故障代码类型',
          key: 'malType',
          align: 'left'
          // render: (h, params) => {
          //   return h('div', params.row.malType === 'NON-VIRTUAL' ? '非虚拟' : '虚拟')
          // }
        },
        {
          title: '对比值',
          key: 'compare',
          align: 'left'
        },
        {
          title: '等级',
          key: 'level',
          align: 'left'
        },
        {
          title: '描述',
          key: 'description',
          align: 'left'
        },
        {
          title: '操作',
          key: 'action',
          width: 250,
          align: 'left',
          render: (h, params) => {
            return h('div', [
              this.$hasAccess('code.edit') &&
                h(
                  'span',
                  {
                    class: 'primary',
                    style: {
                      marginLeft: 0,
                      color: '#0084FF'
                    },
                    on: {
                      click: () => {
                        this.showDrawer('edit', params)
                      }
                    }
                  },
                  '编辑'
                ),
              this.$hasAccess('code.del') &&
                h(
                  'span',
                  {
                    class: 'secondary',
                    style: {
                      color: '#F12121'
                    },
                    on: {
                      click: () => {
                        // console.log(params)
                        this.deleteInfo(params.row)
                      }
                    }
                  },
                  '删除'
                )
            ])
          }
        }
      ],
      tableData: [],
      pageInfo: {
        pageIndex: 1,
        pageSize: 10,
        totalElements: 0
      },
      id: this.$route.params.id,
      type: ''
    }
  },
  methods: {
    gotoView (params) {
      // console.log(params)
      let form = Object.assign({}, params.row)
      this.$router.push({ name: 'knowledge-detail', params: form })
    },
    showColumn () {
      if (!this.$hasAccess('code.edit') && !this.$hasAccess('code.del')) {
        this.tableColumns.splice(this.tableColumns.length - 1, 1)
      }
    },
    onDeviceTypeChange (data) {
      this.formValue.modelId = ''
    },
    passCancel () {
      this.modalNew = false
    },
    onDeviceModelChange (data) {
      // console.log("onDeviceModelChange", data);
    },
    newClick () {
      this.title = this.$t_p('add_fault_code')
      this.formValue = {}
      this.modalNew = true
      this.$refs.formFilter.resetFields()
    },
    asyncOK () {
      this.loadingNew = true
      if (this.formValue.id) {
        getEditCode({...this.formValue}).then(
          (res) => {
            this.loadingNew = false
            this.modalNew = false
            if (res.code === 200) {
              this.$Message.success(this.$t('common.message.edit_success'))
              this.loadTableData()
              this.$refs.drawer.close()
            } else {
              if (res.code === 1000) {
                this.$Message.warning(res.msg)
              } else {
                this.$Message.warning(this.$t('common.message.edit_fail'))
              }
            }
          }
        )
      } else {
        getNewCode({...this.formValue}).then(
          (res) => {
            this.loadingNew = false
            this.modalNew = false
            if (res.code === 200) {
              this.$Message.success(this.$t('common.message.add_success'))
              this.loadTableData()
              this.$refs.drawer.close()
            } else {
              if (res.code === 1000) {
                this.$Message.warning(res.msg)
              } else {
                this.$Message.warning(this.$t('common.message.add_fail'))
              }
            }
          }
        )
      }
    },
    handleSearch () {
      this.serLoading = true
      this.pageInfo.pageIndex = 1
      this.loadTableData()
      //   this.insideTableData = this.value.filter(item => item[this.searchKey].indexOf(this.searchValue) > -1)
    },
    loadTableData (clear) {
      this.loading = true
      if (clear) {
        this.pageInfo.pageIndex = 1
      }
      // this.pageInfo.totalElements = this.tableData.length;
      let data = {
        pageSize: this.pageInfo.pageSize,
        pageNumber: this.pageInfo.pageIndex,
        name: this.formFilter.name
      }
      getErrList(data).then((res) => {
        this.loading = false
        this.serLoading = false
        this.tableData = (res.data.result || []).map((d) => ({
          ...d
        }))
        this.pageInfo.totalElements = res.data.totalElements || 0
      })
    },
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    },
    editInfo (params) {
      // this.formNew = Object.assign({}, params.row);
      let val = JSON.parse(JSON.stringify(params))
      this.formValue = {...val, modelId: params.row.modelId.toString(), typeId: params.row.typeId.toString()}
      this.modalNew = true
    },
    deleteInfo (uInfo) {
      this.$Modal.confirm({
        title: this.$t('common.text.prompt'),
        width: this.$flexible(450),
        content:
          '确认删除故障代码' + `[${uInfo.malCode}]吗?`,
        onOk: () => {
          deleteCodeInfo({
            id: uInfo.id
          }).then(
            () => {
              this.$Message.success(this.$t('common.message.delete_success'))
              this.loadTableData()
            },
            () => {
              this.$Message.warning(this.$t('common.message.delete_fail'))
            }
          )
        }
      })
    },
    showDrawer (type, parmas) {
      this.type = type
      this.$refs.drawer.open()
      if (type === 'add') {
        this.title = '新建设备型号'
        this.formValue = {}
      } else {
        let { row } = parmas
        let val = JSON.parse(JSON.stringify(row))
        this.title = '编辑设备型号'
        this.formValue = val
      }
    },
    confirm (flag) {
      if (flag) {
        this.asyncOK()
      } else {
        this.isBtnLoading = false
      }
    },
    getType () {
      // getDeviceTypeList().then(res => {
      //   let { code, data } = res
      //   if (code === 200) {
      //     this.formList[2].data = data.map(i => {
      //       return {
      //         name: i.typeName,
      //         value: i.id
      //       }
      //     })
      //   }
      // })
      // deviceModel().then(res => {
      //   if (res.code === 200) {
      //     this.formList[3].data = res.data.map(i => {
      //       return {
      //         name: i.modelName,
      //         value: i.id
      //       }
      //     })
      //   }
      // })
    }
  },
  mounted () {
    this.showColumn()
    this.getType()
    this.loadTableData(true)
  }
}
</script>

<style lang="less" scoped>
@import './../../../style/varient';

@size-padding: 10px;
.fault-code-manage {
  width: 100%;
  height: 100%;

  .content {
    width: 100%;
    height: 100%;
    overflow: auto;
    padding: 24px;
    margin-bottom: 10px;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
  }
}
</style>



