import axios from '@/libs/api.request'

/**
 *  获取所有制造商
 * @param pageNumber 页码
 * @param pageSize 页数
 * @param name 页数
 * @returns {*}
 */
export const getManuList = ( {pageNumber, pageSize, name}) =>  {
  const data =  {
    pageNumber,
    pageSize,
    name
  }; 
  return axios.request( {
    url:'equipment/listEquipment', 
    params:data, 
    method:'get'
  })
}; 


/**
 *  新建制造商
 * @param manufactruer 制造商
 * @param contact 联系人
 * @param phone 电话
 * @param email 邮箱
 * @param fax 传真
 * @param address 地址
 * @param remarks 备注
 * @returns {*}
 */
export const getNewManu = ( {manufactruer, contact, phone, email, fax, address, remarks}) =>  {
   let data =  {manufactruer, contact, phone, email, fax, address, remarks}
    return axios.request( {
      url:'equipment/addEquipment', 
      data, 
      method:'post'
    })
  }; 

  /**
 *  编辑制造商
 * @param id id
 * @param manufactruer 制造商
 * @param contact 联系人
 * @param phone 电话
 * @param email 邮箱
 * @param fax 传真
 * @param address 地址
 * @param remarks 备注
 * @returns {*}
 */
export const getEidtManu = ( {id, manufactruer, contact, phone, email, fax, address, remarks}) =>  {
  let data =  {id, manufactruer, contact, phone, email, fax, address, remarks}
   return axios.request( {
     url:'equipment/editEquipment', 
     data, 
     method:'post'
   })
 }; 

     /**
 * 删除制造商
 * @param id id
 * @returns {*}
 */
export const deleteManuInfo = ({id}) => {
  const data = {
      id
    };
  return axios.request({
    url: 'equipment/delEquipment',
     data,
    method: 'post'
  })
};








